import React, { useState } from 'react'
import { Container, Table } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import { InputLabel, NativeSelect} from '@mui/material'
import { Link } from 'react-router-dom'
import ReactSelect from 'react-select'
import { Input } from '@mui/base'
import Pagination from '../../../ReusableComponents/CardsWrap/Paginated/Pagination'
const vacData = [
  { id: 1, name: '1', type: '1', employer: ' 1', municipality: '', date: '10-11-2014', deadline: '10-12-2014', sphere: '' },
  { id: 2, name: '2', type: '2', employer: ' 2', municipality: '', date: '10-11-2014', deadline: '10-12-2014', sphere: '' },
  { id: 3, name: '3', type: '3', employer: ' 3', municipality: '', date: '10-11-2014', deadline: '10-12-2014', sphere: '' },
  { id: 4, name: '4', type: '4', employer: ' 4', municipality: '', date: '10-11-2014', deadline: '10-12-2014', sphere: '' },
  { id: 5, name: '5', type: '5', employer: ' 5', municipality: '', date: '10-11-2014', deadline: '10-12-2014', sphere: '' }
]
const options = [
  { value: 'chocolate', label: 'Chocolate' },
  { value: 'strawberry', label: 'Strawberry' },
  { value: 'vanilla', label: 'Vanilla' }
]
const itemsPerPage = 5;
const colors = ['success', 'danger', 'info', 'warning']
const MyVacancies = () => {
  const { t } = useTranslation();
  const [currentPage, setCurrentPage] = useState(1);

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = vacData.slice(indexOfFirstItem, indexOfLastItem);
  return (
    <Container className='stats_container myVacancies' style={{ backgroundColor: '#fff', padding: 20 }}>
      <div className="filter">
        <ReactSelect
          isMulti
          options={options}
          className="basic-multi-select"
          classNamePrefix="select"
          isSearchable={false}
          placeholder=" "
        />
        <div className='dateElements'>
          <Input type='date' />
          <InputLabel>
            -
          </InputLabel>
        </div>
        <div className='dateElements'>
          <Input type='date' />
          <InputLabel>
            -
          </InputLabel>
        </div>
        <button></button>
      </div>
      <div className='sortAmount_filter'>
        <div className='amount_wrapper'>
          <NativeSelect
            defaultValue={10}
            inputProps={{
              name: 'filterAmount',
              id: 'uncontrolled-native',
            }}
          >
            <option value={10}>10</option>
            <option value={20}>20</option>
            <option value={30}>30</option>
            <option value={40}>40</option>
            <option value={50}>50</option>
          </NativeSelect>
        </div>
        <div className='sort_wrapper'>
          <InputLabel>
            
          </InputLabel>
          <NativeSelect
            defaultValue={10}
            inputProps={{
              name: 'sort',
              id: 'uncontrolled-native',
            }}
          >
            <option value={10}>ID</option>
            <option value={20}></option>
            <option value={30}></option>
            <option value={40}></option>
            <option value={50}></option>
            <option value={60}></option>
            <option value={70}></option>
          </NativeSelect>
        </div>

      </div>
      <Table striped responsive>
        <thead>
          <tr>
            <th>{t('ID')}</th>
            <th>{t('name')}</th>
            <th>{t('type')}</th>
            <th>{t('employer')}</th>
            <th>{t('municipality')}</th>
            <th>{t('date')}</th>
            <th>{t('deadline')}</th>
            <th>{t('')}</th>
          </tr>
        </thead>
        <tbody>
          {currentItems.map((vac) => (
            <tr key={vac.id}>
              <td>{vac.id}</td>
              <td><Link to={`/vacancy/${vac.id}`} target="_blank" >{vac.name}</Link></td>
              <td>{vac.type}</td>
              <td>{vac.employer}</td>
              <td>{vac.municipality}</td>
              <td>{vac.date}</td>
              <td>{vac.deadline}</td>
              <td><div className={`bg-${colors[vac.id - 1] ? colors[vac.id - 1] : 'primary'} text-white`} style={{ padding: 5, borderRadius: 5, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}> <i className='fa-solid fa-check' /></div></td>
            </tr>
          ))}
        </tbody>
      </Table>
      <Pagination
        totalPosts={vacData.length}
        postsPerPage={itemsPerPage}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Container>
  )
}

export default MyVacancies