import React from 'react'
import '../../../pages/blog/profClassification/classification.scss'
import { Container } from 'react-bootstrap'
import Breadcrumbs from '../../../ReusableComponents/breadcrumbs/Breadcrumbs'
import { Link as LinkOfDom } from 'react-router-dom'
import { Element } from 'react-scroll';

const StructureOfSite = () => {
  return (
    <Container style={{ backgroundColor: '#fff', padding: 30 }}>
      <Breadcrumbs />
      <div className='profs'>
        <Element>
          <div>
            <h3> </h3>
            <li><LinkOfDom target='_blank' to='/about/baseInfo'> </LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/about/brand'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/about/partners'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/about/structure'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/about/statistics'></LinkOfDom></li>
          </div>
        </Element>

        <Element>
          <div>
            <h3></h3>
            <li><LinkOfDom target='_blank' to='/services/projects'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/services/grants'>  </LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/services/migration'></LinkOfDom></li>
          </div>
        </Element>

        <Element>
          <div>
            <h3> </h3>
            <li><LinkOfDom target='_blank' to='/blog/articles'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/blog/vid-lessons'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/blog/profesiograma'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/blog/calendar'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/blog/classification'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/blog/researches'></LinkOfDom></li>
          </div>
        </Element>

        <Element>
          <div>
            <h3></h3>
            <li><LinkOfDom target='_blank' to='/media/gallery'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/media/news'> </LinkOfDom></li>
          </div>
        </Element>
        
        <Element>
          <div>
            <h3></h3>
            <li><LinkOfDom target='_blank' to={"/vacancy"}></LinkOfDom></li>
          </div>
        </Element>

        <Element>
          <div>
            <h3></h3>
            <li><LinkOfDom target='_blank' to='/guide/qA'>-</LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/guide/userManual'> </LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/guide/siteStructure'> </LinkOfDom></li>
          </div>
        </Element>

        <Element>
          <div>
            <h3> </h3>
            <li><LinkOfDom target='_blank' to='/pubInfo/law'></LinkOfDom></li>
            <li><LinkOfDom target='_blank' to='/pubInfo/information'> </LinkOfDom></li>
          </div>
        </Element>

      </div>

    </Container>
  )
}

export default StructureOfSite