import { Container } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Breadcrumbs from '../../../ReusableComponents/breadcrumbs/Breadcrumbs';
import './pr.scss'
import { TextareaAutosize } from '@mui/base';

function ProjectsSingle() {
  return (
    <Container style={{ backgroundColor: '#fff', padding: 20 }}>
      <Breadcrumbs />
      <Form>
        <Row className="mb-3">
          <Form.Group as={Col} controlId="formGridEmail">
            <Form.Label></Form.Label>
            <Form.Control placeholder="Enter email" />
          </Form.Group>

          <Form.Group as={Col} controlId="formGridPassword">
            <Form.Label></Form.Label>
            <Form.Control placeholder="Password" />
          </Form.Group>
        </Row>
        <Row>
          <Form.Group as={Col} controlId="pn">
            <Form.Label> </Form.Label>
            <Form.Control type='number' />
          </Form.Group>
        </Row>
        <Row>
          <Form.Group as={Col} controlId="tarigi">
            <Form.Label> </Form.Label>
            <Form.Control type='date' placeholder="date" />
          </Form.Group>
        </Row>
        <Form.Group as={Row} className="mb-3" >
          <Form.Label as="legend" column sm={12} className='text-center'>
            
          </Form.Label>
          <Col sm={12} style={{ display: 'flex', gap: 20, justifyContent: 'center' }}>
            <Form.Check
              type="radio"
              label="."
              name="formHorizontalRadios"
              id="formHorizontalRadios1"
            />
            <Form.Check
              type="radio"
              label="."
              name="formHorizontalRadios"
              id="formHorizontalRadios2"
            />
          </Col>
        </Form.Group>
        <Form.Group className="mb-3" controlId="formGridAddress1">
          <Form.Label> </Form.Label>
          <Form.Control placeholder="1234 Main St" />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formGridAddress1">
          <Form.Label> </Form.Label>
          <Form.Control placeholder="1234 Main St" />
        </Form.Group>

        <Form.Group className="mb-3" controlId="mob">
          <Form.Label></Form.Label>
          <Form.Control type='number' placeholder="+995" />
        </Form.Group>

        <Form.Group className="mb-3" controlId="secmob">
          <Form.Label> </Form.Label>
          <Form.Control type='number' placeholder="" />
        </Form.Group>

        <Form.Group className="mb-3" controlId="fosta">
          <Form.Label>. </Form.Label>
          <Form.Control type='email' placeholder="@" />
        </Form.Group>
        <Form.Group className="mb-3" controlId="fosta">
          <Form.Label> </Form.Label>
          <Form.Control type='email' placeholder="@" />
        </Form.Group>
        <Form.Group controlId="formGridState">
          <Form.Label>  </Form.Label>
          <Form.Select defaultValue={0}>
            <option value={0}>...</option>
            <option>a</option>
            <option>b</option>
            <option>c</option>
          </Form.Select>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" >
          <Form.Label as="legend" column sm={12} className='text-center'>
             
          </Form.Label>
          <Col sm={12} style={{ display: 'flex', gap: 20, justifyContent: 'center' }}>
            <Form.Check
              type="radio"
              label=""
              name="formHorizontalRadios"
              id="formHorizontalRadios1"
            />
            <Form.Check
              type="radio"
              label=""
              name="formHorizontalRadios"
              id="formHorizontalRadios2"
            />
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3" >
          <Form.Label as="legend" column sm={12} className='text-center'>
             
          </Form.Label>
          <Form.Select defaultValue={0}>
            <option value={0}></option>
            <option>a</option>
            <option>b</option>
            <option>c</option>
          </Form.Select>

          <Form.Select defaultValue={0}>
            <option value={0}></option>
            <option>a</option>
            <option>b</option>
            <option>c</option>
          </Form.Select>



          <Form.Group as={Col} controlId="formGridZip" style={{display:'flex', alignItems: 'center', flexDirection:'column'}}>
            <Form.Label> </Form.Label>
            <textarea className='w-100' placeholder=''></textarea>
          </Form.Group>
        </Form.Group>

        <Button variant="primary" type="submit">
          Submit
        </Button>
      </Form>
    </Container>

  );
}

export default ProjectsSingle;